@import 'global';

.initiative-tracker {
  padding: $spacing-3;

  .initiative-tracker__header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 $spacing-1;

    .initiative-tracker__header-title {
      flex-grow: 2;
      margin: $spacing-1;
      font-size: $font-size-18;
      font-weight: $font-bold;
    }
  }

  .initiative-tracker__body {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: $spacing-3 0;
    border-top: 1px solid $light-2-grey;
    border-bottom: 1px solid $light-2-grey;

    .initiative-tracker__no-data {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 80px;
    }

    .initiative-tracker__table {
      width: 100%;
      border-spacing: 0;

      thead {
        height: 40px;
      }

      th,
      td {
        padding: 0 $spacing-1;
      }

      tbody tr:nth-child(odd) {
        background-color: $light-3-grey;
      }

      .initiative-tracker__hp-header {
        width: 0;
        min-width: 70px;
      }

      .initiative-tracker__initiative-header {
        width: 0;
        min-width: 70px;
      }

      .initiative-tracker__actions-header {
        width: 0;
      }

      .initiative-tracker__actions-container {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding: 0;

        &.initiative-tracker__actions-container--desktop-only {
          @media screen and (max-width: 599px) {
            display: none;
          }
        }

        &.initiative-tracker__actions-container--mobile-only {
          @media screen and (min-width: 600px) {
            display: none;
          }
        }
      }
    }
  }

  .initiative-tracker__footer {
    display: flex;
    justify-content: right;
    align-items: center;
    padding: 0 $spacing-1;
  }
}